





































































import { Component, Vue } from 'vue-property-decorator';
import { AlertsModule } from '@/store/modules/alerts';
import { VkModule } from '@/store/modules/vk';
import { WebsocketModule } from '@/store/modules/websocket';
import { mainColor, routePathOnGithubPages } from '@/models/Constants';
import Header from '@/components/Header.vue';

@Component({
	components: {
		Header,
	}
})
export default class App extends Vue {
	mainColor = mainColor;

	get title() {
		return '0.1.62 - ' + (WebsocketModule.isAuth ? 'auth' : 'not auth');
		// return '0.1.37 - ' + (WebsocketModule.isConnected ? 'connected' : 'disconnected');
	}

	initApp() {
		const vk_params = {};
		Object.keys(this.$route.query).sort().forEach((key) => {
			if (key.slice(0, 3) === 'vk_') {
				vk_params[key] = this.$route.query[key];
			}
		});
		const vk_sign = this.$route.query.sign;
		console.log({ vk_params, vk_sign });

		VkModule.init({ vk_params, vk_sign });
		//@ts-ignore
		WebsocketModule.Init({ eventListener: this.$options.sockets });
	}

	created(): void {
		this.initApp();
	}

