	get info() {
		return VkModule.vkParams.vk_platform;
	}

	get error() {
		return AlertsModule.error;
	}

	get message() {
		return AlertsModule.message;
	}

	get showBtnBack() {
		// console.log('this.$route.path', this.$route.path);
		return !(this.$route.path === '/' || this.$route.path === routePathOnGithubPages);
	}

	closeError() {
		AlertsModule.clearError();
	}

	closeMessage() {
		AlertsModule.clearMessage();
	}

