































































































import { Component, Vue } from 'vue-property-decorator';
import { AlertsModule } from '@/store/modules/alerts';
import { VkModule } from '@/store/modules/vk';
import { WebsocketModule } from '@/store/modules/websocket';
import { routePathOnGithubPages } from '@/models/Constants';


@Component
export default class Header extends Vue {

	get title() {
		return '0.1.62 - ' + (WebsocketModule.isAuth ? 'auth' : 'not auth');
		// return '0.1.37 - ' + (WebsocketModule.isConnected ? 'connected' : 'disconnected');
	}

	get showBtnBack() {
		// console.log('this.$route.path', this.$route.path);
		return !(this.$route.path === '/' || this.$route.path === routePathOnGithubPages);
	}


}
